.landing-mobile {
  height: 90vh;
  top: 10vh;
  position: absolute;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  img {
    width: 100vw;
    height: 60vh; } }

.landing-mobile-link {
  color: rgb(255, 255, 255);
  text-decoration: none; }

.landing-mobile-1, .landing-mobile-2 {
  height: 30vh;
  width: 50vw;
  display: flex;
  align-items: center;
  justify-content: center; }

.landing-mobile-1 {
  background-color: rgb(16, 144, 0); }

.landing-mobile-2 {
  background-color: rgb(40, 39, 125); }
